<template>
  <div class="container">
    <div class="main__cars" v-if="keys">
      <div class="main__cars-item" v-for="lade in keys" :key="lade.id">
        <p>userId: {{ lade.userId }}</p>
        <p>Id: {{ lade.id }}</p>
        <p>Title: {{ lade.title }}</p>
        <p>body: {{ lade.body }}</p>
      </div>
    </div>
    <div v-else>Идет загрузка...</div>
  </div>
</template>

<script>
 
export default {
  data() {
    return {};
  },
  computed: {
    keys() {
      return this.$store.state.keys;
    },
  },

  methods: {
    getPosts() {
      this.$store.dispatch("getPosts");
    }
  },
  created() {
    this.getPosts();
  },
};
</script>
